.todo {
  position: relative;
  display: flex;
  background-color: rgb(255, 255, 255);
  padding: 10px;
  margin: 20px auto;
  border-radius: 5px;
  min-width: 300px;
  width: 40%;
  user-select: none;
}

.todoIcon {
  position: absolute;
  top: 10px;
  width: 17px;
  height: 17px;
  margin-right: 15px;
  padding: 3px 0 0 0;
  color: rgb(29, 147, 84);
}

.deleteIcon {
  position: absolute;
  right: 25px;
  top: 10px;
  width: 17px;
  height: 17px;
  margin-right: 15px;
  padding: 3px 0 0 0;
  color: rgb(29, 147, 84);
  cursor: pointer;
  color: lightgrey;
  transition: transform 0.3s;
}

.checkIcon {
  position: absolute;
  right: 0px;
  top: 10px;
  width: 17px;
  height: 17px;
  margin-right: 15px;
  padding: 3px 0 0 0;
  color: rgb(29, 147, 84);
  cursor: pointer;
  color: lightgrey;
  transition: transform 0.3s;
}

.todoText {
  padding-left: 25px;
  padding-right: 50px;
}

.completedTodo {
  background-color: unset;
  border-color: gray;
  color: gray;
  border: 1px solid gray;
}

.todo.completedTodo .todoIcon,
.todo.completedTodo .checkIcon,
.todo.completedTodo .deleteIcon {
  color: gray;
}

.checkIcon:hover,
.deleteIcon:hover {
  cursor: pointer;
  transform: scale(1.3);
}

.checkIcon:hover {
  color: green;
}

.deleteIcon:hover {
  color: red;
}
