.button {
  position: relative;
  top: 15px;
  right: 10px;
  margin: 0 0 15px 20px;
  height: 30px;
  width: 30px;
  cursor: pointer;
  background-color: rgb(255, 255, 255);
  font-size: 1.5rem;
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
}

button:hover {
  background-color: rgb(186, 237, 176);
}

.todoIcon {
  position: relative;
  bottom: 50px;
  width: 17px;
  height: 17px;
  padding: 3px 0 0 0;
}
